<template>
	<div id="register" :style="{backgroundImage:'url('+bg+')'}">
    <ul class="registerBox">
      <router-link to="/register"><li class="registerTxt">注册</li></router-link>

      <router-link to="/login"><li class="loginTxt">登录</li></router-link>

    </ul>
    <form action="" class="formRegister">
      <div class="neiGit"><input type="text" id="userYhm" placeholder="用户名" class="icoUser" v-model="nuserNamer"></div>
      <div class="neiGit"><input type="password" id="userPsd" placeholder="密码" class="icoPswod" v-model="psowed"></div>
      <div class="neiGit"><input type="password" id="userPsdTwo" placeholder="确认密码" class="icoPswodTwo" v-model="psowedTwo"></div>
      <div class="dailiBootm">
        <div class="dailiRight" v-if="show">代理返利&nbsp;<div class="txtBoc"><input type="text" class="txt"></div>‰</div>
        <div class="dailiLeft">
          是否为代理&nbsp;&nbsp;
          <i class="isIco" :class="{ errorRed: isSelect===false}" @click="isIcoBtn" v-show="downIcon"></i><i class="isIco activeImg" :class="{ errorRed: isSelect===false }" @click="isIcoBtn " v-show="!downIcon"><img src="./../assets/images/game101.png" alt=""></i>&nbsp;&nbsp;是&nbsp;
          <i class="isIco" :class="{ errorRed: isSelect===false}" @click="isIcoBtnFase" v-show="fouIco"></i><i class="isIco activeImg" :class="{ errorRed: isSelect===false}" @click="isIcoBtnFase" v-show="!fouIco"><img src="./../assets/images/game101.png" alt=""></i>&nbsp;&nbsp;否&nbsp;
        </div>
      </div>
      <div class="registerBtn" @click="registerBtn">注册</div>
    </form>
    <!-- 底部导航菜单 -->
    <tabs :selected="selected"></tabs>
  </div>
</template>

<script>
import tabs from './../components/tabs'
export default {
  name: 'register',
  data () {
    return {
      bg: require('./../assets/images/game98.png'), // 背景图片
      selected: '', // 导航当前页的状态
      nuserNamer: '', // 用户名
      psowed: '', // 密码
      psowedTwo: '', // 确认密码
      show: false,
      downIcon: true,
      fouIco: true,
      isSelect: null
    }
  },
  components: {
    tabs
  },
  methods: {
    registerBtn () {
      var that = this
      if (this.nuserNamer === '') {
        document.getElementById('userYhm').setAttribute('style', 'border: 1px solid red;')
        return false
      } else {
        document.getElementById('userYhm').setAttribute('style', 'border: 1px solid #4d2e2a;')
      }
      if (this.psowed === '') {
        document.getElementById('userPsd').setAttribute('style', 'border: 1px solid red;')
        return false
      } else {
        document.getElementById('userPsd').setAttribute('style', 'border: 1px solid #4d2e2a;')
      }
      if (this.psowedTwo === '' || (this.psowed !== this.psowedTwo)) {
        document.getElementById('userPsdTwo').setAttribute('style', 'border: 1px solid red;')
        return false
      } else {
        document.getElementById('userPsdTwo').setAttribute('style', 'border: 1px solid #4d2e2a;')
      }
      if (this.isSelect === null) {
        this.isSelect = false
        return false
      }
      that.$router.push({ path: '/' })
    },
    isIcoBtn () {
      this.show = true
      this.downIcon = !this.downIcon
      this.fouIco = true
      this.isSelect = true
    },
    isIcoBtnFase () {
      this.show = false
      this.downIcon = true
      this.fouIco = !this.fouIco
      this.isSelect = true
    }
  }
}
</script>

<style scoped>
  #register{background-size: 100% 100%;height: 100%;position: fixed;width: 100%;}
  .registerBox{width: 60%; margin: 8rem auto 0 auto;}
  .registerBox li{float: left; width: 50%; text-align: center; font-size: 0.8rem;padding: 0.4rem 0;}
  .registerTxt{color: #fff;border-bottom: 1px solid #715d58}
  .loginTxt{color: #9a9a9a;}
  .formRegister{display: inline-block; width: 80%;margin-left: 10%; margin-top: 1rem;}
  .formRegister .neiGit{display: inline-block; width: 100%;margin-bottom: 0.6rem;}
  .formRegister .neiGit input{width: 100%;border: 1px solid #4d2e2a; outline: none;color: #fff; font-size: 0.8rem; border-radius: 6px;height: 2rem;}
  .formRegister .neiGit input::-webkit-input-placeholder{color:#9b9b9b;}
  .formRegister .neiGit input::-moz-placeholder{color:#9b9b9b;}
  .formRegister .neiGit input:-moz-placeholder{color:#9b9b9b;}
  .formRegister .neiGit input:-ms-input-placeholder{color:#9b9b9b;}
  .formRegister .neiGit .icoUser{background:#4d2e2a url("./../assets/images/game99.png") no-repeat 0.3rem 0.3rem; background-size: 7%; text-indent: 2rem;}
  .formRegister .neiGit .icoPswod{background:#4d2e2a url("./../assets/images/game100.png") no-repeat 0.3rem 0.3rem; background-size: 7%; text-indent: 2rem;}
  .formRegister .neiGit .icoPswodTwo{background:#4d2e2a url("./../assets/images/game100.png") no-repeat 0.3rem 0.3rem; background-size: 7%; text-indent: 2rem;}

  .dailiBootm{color: #fff;display: inline-block;width: 100%;height: 3rem;}
  .dailiLeft{float: left;font-size: 0.7rem;vertical-align: middle;margin-top: 0.6rem;}
  .dailiRight{float: right;font-size: 0.7rem;max-width: 40%;text-align: right;}
  .isIco{display:inline-block; vertical-align: middle; margin-top: -0.15rem; background: #795d5a; width: 1rem; height: 1rem; border: 1px solid #9d8987; border-radius: 4px;}
  .txtBoc{width: 30%; display: inline-block; border-bottom: 1px solid #795d5a; margin: 0 0.2rem;}
  .dailiRight .txt{border: 0; width: 100%; outline: none; background: none; color: #fff; font-size: 0.8rem; border-radius: 6px;height: 2rem;}
  .activeImg{position: relative;}
  .activeImg img{position: absolute; width: 1rem;}
  .registerBtn{width: 100%;background: #8d170d; color: #fff; text-align: center; padding: 0.5rem 0;margin-top: 2rem; border-radius: 5px;}
  .errorRed{border: 1px solid red;}
</style>



